'use strict';
const angular = require('angular');

export default angular.module('scApp.leftNav', [])
  .directive('leftNav', function() {
    return {
      templateUrl: 'app/leftNav/leftNav.html',
      // templateUrl: 'nav.html',
      restrict: 'EA',
      link: function(scope, element, attrs) {},
      controller: function($http, $state){
        var vm = this;
        vm.bookmarks = [];
        vm.getBookMarks = getBookMarks.bind(this);
        vm.getBookMarks();
        function getBookMarks(){
          $http.get('/api/bookmarks')
            .then((response) =>{
              vm.bookmarks = response.data;
            })
        }
        vm.bookmark = function(){
          $http.post('/api/bookmarks',{name: $state.current.name})
            .then((response) =>{
              console.log(response);
            })
        }
      },
      controllerAs: 'vm'
    };
  })
  .name;



