	module.exports = "\r\n<div class=\"row\" ng-class=\"{'otherChapter': ($ctrl.url == '/home/introduction' || $ctrl.url =='/home/force' || $ctrl.url == '/home/gravity' || $ctrl.url == '/home/restricted' || $ctrl.url == '/home/tooth' || $ctrl.url == '/home/moment-definition' || $ctrl.url == '/home/moment' || $ctrl.url == '/home/magnitude-of-force' || $ctrl.url == '/home/direction-of-force' || $ctrl.url == '/home/transmissibility' || $ctrl.url == '/home/adding-force-together' || $ctrl.url == '/home/component-of-force' || $ctrl.url == '/home/restricted-objects' || $ctrl.url == '/home/moment-force' || $ctrl.url == '/home/characteristics' || $ctrl.url == '/home/review-question' || $ctrl.url == '/home/summary' || $ctrl.url == '/home/recommended-reading' || $ctrl.url == '/home/chapter2-introduction' || $ctrl.url == '/home/chapter2-tooth-movement' || $ctrl.url == '/home/chapter02-equivalent-system' || $ctrl.url == '/home/chapter02-equivalent-usage' || $ctrl.url == '/home/chapter02-couple-definition')}\">\r\n\r\n  <left-nav>\r\n  </left-nav>\r\n  <div class=\"col-sm-9\">\r\n    <ui-view></ui-view>\r\n  </div>\r\n</div>\r\n<script type=\"text/ng-template\" id=\"nav.html\">\r\n    <div class=\"col-sm-3 nav-item\">\r\n    <div class=\"sidebar-nav\">\r\n      <div class=\"navbar navbar-default\" role=\"navigation\">\r\n        <div class=\"navbar-header\">\r\n          <button type=\"button\" class=\"navbar-toggle\" data-toggle=\"collapse\" data-target=\".sidebar-navbar-collapse\">\r\n            <span class=\"sr-only\">Toggle navigation</span>\r\n            <span class=\"icon-bar\"></span>\r\n            <span class=\"icon-bar\"></span>\r\n            <span class=\"icon-bar\"></span>\r\n          </button>\r\n          <span class=\"visible-xs navbar-brand\">Mechanotherapy</span>\r\n        </div>\r\n        <div class=\"navbar-collapse collapse sidebar-navbar-collapse\">\r\n          <ul class=\"nav navbar-nav\">\r\n            <span><h4>Mechanotherapy</h4></span>\r\n            <li ><a href=\"#\" data-toggle=\"collapse\" data-target=\"#first\">Front Matter</a>\r\n              <ul id=\"first\"  class=\"collapse\">\r\n                <li><a ui-sref=\"home.title\">Title</a></li>\r\n                <li><a ui-sref=\"home.copyright\">Copyright</a></li>\r\n                <li><a ui-sref=\"home.foreword\">Foreword</a></li>\r\n                <li><a ui-sref=\"home.preface\">Preface</a></li>\r\n                <li><a ui-sref=\"home.ack\">Acknowledgements</a></li>\r\n                <li><a ui-sref=\"home.contributors\">Contributors</a></li>\r\n\r\n              </ul>\r\n            </li>\r\n            <li><a href=\"#\" data-toggle=\"collapse\" data-target=\"#second\">Chapter 1<br>Force and Moment</a>\r\n            <ul id=\"second\"  class=\"collapse\">\r\n                <li><a ui-sref=\"home.intro\">Introduction</a></li>\r\n<!--                 <li><a href=\"#\" data-toggle=\"collapse\" data-target=\"#third\" >Center of residence</a>\r\n -->                <li><a data-toggle=\"collapse\" data-target=\"#third\">Force</a>\r\n                      <ul id=\"third\" class=\"collapse\">\r\n                        <li><a ui-sref=\"home.force\">Definition</a></li>\r\n                        <li><a ui-sref=\"home.magnitude-of-force\">Magnitude of Force</a></li>\r\n                        <li><a ui-sref=\"home.direction-of-force\">Direction of Force</a></li>\r\n                        <li><a ui-sref=\"home.transmissibility\">Law of Transmissibility</a></li>\r\n                        <li><a ui-sref=\"home.adding-force-together\">Adding Force Together</a></li>\r\n                        <li><a ui-sref=\"home.component-of-force\">Component of Force</a></li>\r\n                      </ul>\r\n                    </li>\r\n                    <li><a data-toggle=\"collapse\" data-target=\"#fourth\">Center of Resistance</a>\r\n                     <ul id=\"fourth\" class=\"collapse\" >\r\n                       <li><a ui-sref=\"home.gravity\">Center of Gravity</a></li>\r\n                       <li><a ui-sref=\"home.restricted-objects\">Restricted Objects</a></li>\r\n                       <li><a ui-sref=\"home.tooth\">Tooth as a Restricted Object</a></li>\r\n                     </ul>\r\n                    </li>\r\n                    <li><a href=\"#\" data-toggle=\"collapse\" data-target=\"#fifth\">Moment</a>\r\n                      <ul id=\"fifth\" class=\"collapse\">\r\n                        <li><a ui-sref=\"home.moment-definition\">Definition</a></li>\r\n                        <li><a ui-sref=\"home.moment-force\">Moment of Force</a></li>\r\n                        <li><a ui-sref=\"home.characteristics\">Characteristics</a></li>\r\n\r\n                      </ul>\r\n                      </a></li>\r\n                    <li><a ui-sref=\"home.review-question\">Review Question</a></li>\r\n                    <li><a ui-sref=\"home.summary\">Summary</a></li>\r\n                    <li><a ui-sref=\"home.recommended-reading\">Recommended Reading</a></li>\r\n\r\n\r\n                </li>\r\n\r\n              </ul>\r\n\r\n            <li><a href=\"#\" data-toggle=\"collapse\" data-target=\"#sixth\">Chapter 2<br/>Couple</a>\r\n              <ul id=\"sixth\" class=\"collapse\">\r\n                <li><a ui-sref=\"home.chapter2-introduction\">Introduction</a></li>\r\n                <li><a ui-sref=\"home.chapter2-tooth-movement\">Tooth Movement</a></li>\r\n                <li><a href=\"\">Equivalent System</a></li>\r\n                <li><a href=\"\">Couple</a></li>\r\n                <li><a href=\"\">Review Questions</a></li>\r\n                <li><a href=\"\">Summary</a></li>\r\n                <li><a href=\"\">Recommend Reading</a></li>\r\n\r\n              </ul>\r\n            </li>\r\n            <li><a href=\"#\">Menu Item 4</a></li>\r\n            <li><a href=\"#\">Reviews <span class=\"badge\">1,118</span></a></li>\r\n          </ul>\r\n        </div>\r\n      </div>\r\n  </div>\r\n</div>\r\n</script>\r\n<script type=\"text/ng-template\" id=\"summary.html\">\r\n  <h1 class=\"tit_3\"><a id=\"11\"></a><span>Summary</span></h1>\r\n<p class=\"indent\"><span>In this chapter, we discussed forces and moments and their characteristics. These are the two main elements of any orthodontic movement. Whether you are using orthopedic or orthodontic appliances, the end result is the delivery of a combination of forces and moments to teeth and jaws.</span></p>\r\n<p class=\"indent\"><span>In the next chapter, we analyze the effect of the force and the moment on the center of resistance by introducing the concept of the equivalent system. We also discuss how to produce a moment without changing the point of application of force, by defining the concept of couple.</span></p><br />\r\n</script>\r\n<script type=\"text/ng-template\" id=\"review-question.html\">\r\n  <h1 class=\"tit_1\"><a id=\"10\"></a><span>Review Questions</span></h1>\r\n<div class=\"box3\">\r\n<p class=\"box1\">Question 1</p>\r\n</div>\r\n<p class=\"normal3\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span>From a biomechanical point of view, why is it important to know if the patient has bone loss?</span></p>\r\n<table style=\"width:100%;\">\r\n<tr>\r\n<td>\r\n<div class=\"review1A\" style=\"float:left;\">\r\n<p class=\"img\" style=\"margin-top:10px;\"><a id=\"playr1\" onclick=\"playr1()\"><img id=\"imgr1\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q1A/Q1A0000.png\"/></a></p>\r\n</div>\r\n</td>\r\n<td>\r\n<div class=\"review1B\" style=\"float:right;\">\r\n<p class=\"img\" style=\"margin-top:10px;\"><a id=\"playr1b\" onclick=\"playr1b()\"><img id=\"imgr1b\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q1B/Q1B0000.png\"/></a></p>\r\n</div>\r\n</td>\r\n</tr>\r\n</table>\r\n<div class=\"btn_answer_show\">\r\n<p style=\"margin-top:2em;\"><img style=\"max-width:100%;\" id=\"img1\" src=\"/assets/images/answer_show.png\" onclick=\"toggle_visibility1('foo1');\"/></p>\r\n</div>\r\n<div style=\"display: none;\" id=\"foo1\">\r\n<p class=\"indent\"><span>Alveolar bone loss will increase the distance between the force and the center of resistance. Under these conditions, application of force to a bracket can produce larger moments that can tilt the tooth more than necessary or wanted. These larger moments in patients with generalized bone loss can also act as unnecessarily increased stress on bone and cause further bone loss. </span></p>\r\n</div>\r\n<div class=\"box2\">\r\n<p class=\"box1\">Question 2</p>\r\n</div>\r\n<p class=\"normal3\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span>Which force these forces is more effective in correcting the inclination of a molar (assuming the magnitude of forces are similar)?</span></p>\r\n<table style=\"width:100%; margin-bottom:-2em\">\r\n<tr>\r\n<td>\r\n<div class=\"review2A\" style=\"float:left;\">\r\n<p class=\"img\"><a id=\"playr2\" onclick=\"playr2()\"><img id=\"imgr2\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q2A/Q2A0000.png\"/></a></p></div>\r\n</td>\r\n<td>\r\n<div class=\"review2B\">\r\n<p class=\"img\"><a id=\"playr2b\" onclick=\"playr2b()\"><img id=\"imgr2b\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q2B/Q2B0000.png\"/></a></p></div>\r\n</td>\r\n</tr>\r\n</table>\r\n<table style=\"width:100%; position:absolute\">\r\n<tr>\r\n<td><p style=\"margin-left:8em; margin-top: 0em\"><img src=\"/assets/images/A.png\" /></p></td>\r\n<td><p style=\"margin-left:-35%; margin-top: 0em\"><img id=\"pan2\" src=\"/assets/images/B.png\" /></p></td></tr>\r\n</table>\r\n<div class=\"btn_answer_show\">\r\n<p style=\"margin-top:7em;\"><img style=\"max-width:100%;\" id=\"img2\" src=\"/assets/images/answer_show.png\" onclick=\"toggle_visibility2('foo2');\"/></p>\r\n</div>\r\n<div style=\"display: none;\" id=\"foo2\">\r\n<p class=\"indent\"><span>An horizontal force (B) will produce a larger moment to correct the inclination of the tooth due to the increase in the distance between the force and the center of resistance. Although these forces produce a moment that can change the inclination of the tooth, they also move the tooth in the direction of the force, which may or may not be necessary.</span></p>\r\n</div>\r\n<div class=\"box2\">\r\n<p class=\"box1\">Question 3</p>\r\n</div>\r\n<p class=\"normal3\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span>In which of the following situations does the force produce more rotation?</span></p>\r\n<table style=\"width:100%; margin-bottom:-4em\">\r\n<tr>\r\n<td>\r\n<div style=\"float:left;\">\r\n<p class=\"img\"><a id=\"playr3\" onclick=\"playr3()\"><img id=\"imgr3\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q3A/Q3A0000.png\"/></a></p>\r\n<p style=\"text-align:center; margin-top: 4em\"><img src=\"/assets/images/A.png\" /></p>\r\n</div>\r\n</td>\r\n<td>\r\n<div style=\"float:right;\">\r\n<p class=\"img\"><a id=\"playr3b\" onclick=\"playr3b()\"><img id=\"imgr3b\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q3B/Q3B0000.png\"/></a></p>\r\n<p style=\"text-align:center; margin-top: 4em\"><img id=\"pan\" src=\"/assets/images/B.png\" /></p>\r\n</div>\r\n</td>\r\n</tr>\r\n</table>\r\n<div class=\"btn_answer_show\">\r\n<p style=\"margin-top:7em;\"><img style=\"max-width:100%;\" id=\"img3\" src=\"/assets/images/answer_show.png\" onclick=\"toggle_visibility3('foo3');\"/></p>\r\n</div>\r\n<div style=\"display: none;\" id=\"foo3\">\r\n<p class=\"indent\"><span>When the inclination of a tooth is significant such as in B, the intrusion forces can produce a large moment because the distance between the force and the center of resistance increases.</span></p>\r\n<p class=\"center1\">F &#215; d<sub>2</sub> &gt; F &#215; d<sub>1</sub></p>\r\n<p class=\"center2\">M<sub>B</sub> &gt; M<sub>A</sub></p>\r\n<p class=\"indent\"><span>This is very important to remember because application of intrusion forces on patients with proclined anterior teeth can cause further proclination.</span></p>\r\n</div>\r\n<div class=\"box2\">\r\n<p class=\"box1\">Question 4</p>\r\n</div>\r\n<p class=\"normal3\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span>How will each of these teeth move?</span></p>\r\n<p class=\"img2\"><a id=\"playr4\" onclick=\"playr4()\"><img id=\"imgr4\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q4A/Q4A0000.png\"/></a></p>\r\n<p class=\"img2\"><a id=\"playr4b\" onclick=\"playr4b()\"><img id=\"imgr4b\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q4B/Q4B0000.png\"/></a></p>\r\n<p class=\"img2\"><a id=\"playr4c\" onclick=\"playr4c()\"><img id=\"imgr4c\" style=\"max-width:100%;\" src=\"/assets/images/ch1_Q4C/Q4C0000.png\"/></a></p>\r\n<div class=\"btn_answer_show\">\r\n<p style=\"margin-top:4em;\"><img style=\"max-width:100%;\" id=\"img4\" src=\"/assets/images/answer_show.png\" onclick=\"toggle_visibility4('foo4');\"/></p>\r\n</div>\r\n<div style=\"display: none;\" id=\"foo4\">\r\n<p class=\"indent\"><span>Application of the same magnitudes of force at different points on a molar tooth can produce different types of tooth movement depending on the point of application and the moment generated.</span></p></div>\r\n</script>\r\n<script type=\"text/ng-template\" id=\"characteristics.html\">\r\n  <h2 class=\"level2\"><a id=\"91\"></a><span>Characteristics of Moments</span></h2>\r\n<p class=\"indent\"><span>Although forces can have multiple directions, moments can have only two directions in any particular dimension&#8212;for example, clockwise and counterclockwise (<a href=\"#fig1.37\">Figure 1.37</a>).</span></p>\r\n<table style=\"width:100%; margin-bottom:-1em; margin-top:-2em\">\r\n<tr>\r\n<td>\r\n<p style=\"float:left;\"><a id=\"play37a\" onclick=\"play37a()\"><img id=\"img37a\" style=\"max-width:100%;\" src=\"/assets/images/fig37A/ch1_fig37A0000.png\"/></a></p>\r\n</td>\r\n<td>\r\n<p><a id=\"play37b\" onclick=\"play37b()\"><img id=\"img37b\" style=\"max-width:100%;\" src=\"/assets/images/fig37B/ch1_fig37B0000.png\"/></a></p>\r\n</td>\r\n<td>\r\n<p style=\"float:right;\"><a id=\"play37c\" onclick=\"play37c()\"><img id=\"img37c\" style=\"max-width:100%;\" src=\"/assets/images/fig37C/ch1_fig37C0000.png\"/></a></p>\r\n</td>\r\n</tr>\r\n</table>\r\n<p class=\"caption\" id=\"fig1.37\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.37. Moments do not have multiple directions as forces&#8212;instead, only two.</i></span></p>\r\n<p class=\"indent\"><span>Because moments have only two directions, different forces can produce similar moments. For example, two forces can have similar magnitudes but different directions and still produce similar moments, considering that the magnitude of the moment equals magnitude of force (F) multiplied by distance (d) (<a href=\"#fig1.38\">Figure 1.38</a>). In this example, because both the magnitudes of force and distance are equal, the magnitude of both moments will be equal. Note that for opposite forces to produce the same moments they need to be applied on opposite sides of the center of resistance </span></p>\r\n<p class=\"img\"><a id=\"play38\" onclick=\"play38()\"><img id=\"img38\" style=\"max-width:100%;\" src=\"/assets/images/fig38/ch1_fig380000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig1.38\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.38. Effect of two opposite but equal forces applied at equal distance from center of resistance. Note that the forces are on opposite sides of the center of resistance.</i></span></p>\r\n<p class=\"indent\"><span>Another important characteristic of moments is that by changing the distance between the force and the center of resistance, the magnitude of moment will change. Increasing the force or keeping the force constant but increasing the distance from the center of resistance will have similar effect on the tendency of the tooth to rotate&#8212;that is, create the same moment (<a href=\"#fig1.39\">Figure 1.39</a>).</span></p>\r\n<p class=\"img\"><a id=\"play39\" onclick=\"play39()\"><img id=\"img39\" style=\"max-width:100%;\" src=\"/assets/images/fig39/ch1_fig39_0000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig1.39\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.39. The magnitude of the moment depends on the magnitude of the force and the distance from the center of resistance. Similar moments can be produced by altering the magnitude of the force and the distance of the center of resistance.</i></span></p>\r\n</script>\r\n<script type=\"text/ng-template\" id=\"gravity.html\">\r\n  <h1 class=\"tit_1\"><a id=\"8\"></a><a id=\"502\"></a><span>Center of Resistance</span></h1>\r\n<p class=\"indent\"><span>Every object or free body has one point on which it can be perfectly balanced. This point is called <i>center of gravity.</i></span></p>\r\n<p class=\"indent\"><span>Imagine you are balancing a pencil on your finger. If you move your finger so it is under the pencil’s center of gravity, both the force of gravity and the force applied by your finger will pass through the same point (the center of gravity) and the pencil will be in balance (<a href=\"#fig1.19\">Figure 1.19</a>). </span></p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/fig19.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig1.19\"><span><i>Figure 1.19. The application of a force under the center of gravity of an object can balance it, thereby preventing it from falling down.</i></span></p>\r\n<p class=\"indent\"><span>As soon as you move your finger to one side, the force from your finger does not pass through the center of gravity and the object will not be in balance anymore (<a href=\"#fig1.20\">Figure 1.20</a>).</span></p>\r\n<p class=\"img\"><a id=\"play20\" onclick=\"play20()\"><img id=\"img20\" style=\"max-width:100%;\" src=\"/assets/images/fig20/ch1_fig20_0000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig1.20\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.20. Application of a force that does not pass through the center of gravity causes an object to lose its balance, tilt, and fall.</i></span></p>\r\n<p class=\"indent\"><span>If you apply a larger magnitude of force on the center of gravity of an object you can move it in a straight line without tilting it (<a href=\"#fig1.21\">Figure 1.21</a>).</span></p>\r\n<p class=\"img\"><a id=\"play21\" onclick=\"play21()\"><img id=\"img21\" style=\"max-width:100%;\" src=\"/assets/images/fig21/ch1_fig21_0000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig1.21\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.21. Application of larger magnitude of force on the center of gravity of an object not only prevents it from falling but it can move the object in the direction of the force without tilting it.</i></span></p>\r\n<script>\r\n\t  var startImage20=0;\r\n\t  var endImage20=55;\r\n      var counter21=0;\r\n      var imgPath1_20=\"/assets/images/fig20/ch1_fig20_000\";\r\n      var imgPath2_20=\"/assets/images/fig20/ch1_fig20_00\";\r\n\t  var interval21;\r\n      function play20() {\r\nvar img=document.getElementById(\"img20\");\r\n   \tdocument.getElementById(\"play20\").onclick=\"\";\r\n      \t       \t interval21= setInterval(function(){\r\n      \t   if(counter21<10)\r\n      \t \timg.src=imgPath1_20+counter21+\".png\";\r\n      \t   else\r\n      \t   \timg.src=imgPath2_20+counter21+\".png\";\r\n      \t \tcounter21++;\r\n      \t \tif(counter21>endImage20) {\r\n      \t \t\tclearInterval(interval21);\r\n      \t \t\tcounter21=0;\r\n      \t \t\tdocument.getElementById(\"play20\").onclick=play20;\r\n      \t \t\t//img.src=imgPath1_20+startImage20+\".png\";\r\n      \t \t}\r\n\r\n      \t },100);\r\n      }\r\nfunction stop20(){\r\n      \t \t\tclearInterval(interval21);\r\n      \t \t\tcounter21=0;\r\n      \t \t\tdocument.getElementById(\"play20\").onclick=play20;\r\n      \t \t\timg.src=imgPath2_20+endImage20+\".png\";\r\n}\r\nvar startImage21=0;\r\n\t  var endImage21=55;\r\n      var counter22=0;\r\n      var imgPath1_21=\"/assets/images/fig21/ch1_fig21_000\";\r\n      var imgPath2_21=\"/assets/images/fig21/ch1_fig21_00\";\r\n\t  var interval22;\r\n      function play21() {\r\nvar img=document.getElementById(\"img21\");\r\n      \tdocument.getElementById(\"play21\").onclick=\"\";\r\n      \t       \t interval22= setInterval(function(){\r\n      \t   if(counter22<10)\r\n      \t \timg.src=imgPath1_21+counter22+\".png\";\r\n      \t   else\r\n      \t   \timg.src=imgPath2_21+counter22+\".png\";\r\n      \t \tcounter22++;\r\n      \t \tif(counter22>endImage21) {\r\n      \t \t\tclearInterval(interval22);\r\n      \t \t\tcounter22=0;\r\n      \t \t\tdocument.getElementById(\"play21\").onclick=play21;\r\n      \t \t\t//img.src=imgPath1_21+startImage21+\".png\";\r\n      \t \t}\r\n\r\n      \t },100);\r\n      }\r\nfunction stop21(){\r\n      \t \t\tclearInterval(interval22);\r\n      \t \t\tcounter22=0;\r\n      \t \t\tdocument.getElementById(\"play21\").onclick=play21;\r\n      \t \t\timg.src=imgPath2_21+endImage21+\".png\";\r\n}\r\nvar startImage22=0;\r\n\t  var endImage22=36;\r\n      var counter23=0;\r\n      var imgPath1_22=\"/assets/images/fig22/ch1_fig22_000\";\r\n      var imgPath2_22=\"/assets/images/fig22/ch1_fig22_00\";\r\n\t  var interval23;\r\n      function play22() {\r\nvar img=document.getElementById(\"img22\");\r\n   \tdocument.getElementById(\"play22\").onclick=\"\";\r\n      \t       \t interval23= setInterval(function(){\r\n      \t   if(counter23<10)\r\n      \t \timg.src=imgPath1_22+counter23+\".png\";\r\n      \t   else\r\n      \t   \timg.src=imgPath2_22+counter23+\".png\";\r\n      \t \tcounter23++;\r\n      \t \tif(counter23>endImage22) {\r\n      \t \t\tclearInterval(interval23);\r\n      \t \t\tcounter23=0;\r\n      \t \t\tdocument.getElementById(\"play22\").onclick=play22;\r\n      \t \t\t//img.src=imgPath1_22+startImage22+\".png\";\r\n      \t \t}\r\n\r\n      \t },100);\r\n      }\r\nfunction stop22(){\r\n      \t \t\tclearInterval(interval23);\r\n      \t \t\tcounter23=0;\r\n      \t \t\tdocument.getElementById(\"play22\").onclick=play22;\r\n      \t \t\timg.src=imgPath2_22+endImage22+\".png\";\r\n}\r\nvar startImage23=0;\r\n\t  var endImage23=59;\r\n      var counter24=0;\r\n      var imgPath1_23=\"/assets/images/fig23/ch1_fig23000\";\r\n      var imgPath2_23=\"/assets/images/fig23/ch1_fig2300\";\r\n\t  var interval24;\r\n      function play23() {\r\nvar img=document.getElementById(\"img23\");\r\n      \tdocument.getElementById(\"play23\").onclick=\"\";\r\n      \t       \t interval24= setInterval(function(){\r\n      \t   if(counter24<10)\r\n      \t \timg.src=imgPath1_23+counter24+\".png\";\r\n      \t   else\r\n      \t   \timg.src=imgPath2_23+counter24+\".png\";\r\n      \t \tcounter24++;\r\n      \t \tif(counter24>endImage23) {\r\n      \t \t\tclearInterval(interval24);\r\n      \t \t\tcounter24=0;\r\n      \t \t\tdocument.getElementById(\"play23\").onclick=play23;\r\n      \t \t\t//img.src=imgPath1_23+startImage23+\".png\";\r\n      \t \t}\r\n\r\n      \t },100);\r\n      }\r\nfunction stop23(){\r\n      \t \t\tclearInterval(interval24);\r\n      \t \t\tcounter24=0;\r\n      \t \t\tdocument.getElementById(\"play23\").onclick=play23;\r\n      \t \t\timg.src=imgPath2_23+endImage23+\".png\";\r\n}\r\nvar startImage24a=0;\r\n\t  var endImage24a=31;\r\n      var counter25=0;\r\n      var imgPath1_24a=\"/assets/images/fig24A/ch1_fig24A000\";\r\n      var imgPath2_24a=\"/assets/images/fig24A/ch1_fig24A00\";\r\n\t  var interval25;\r\n      function play24a() {\r\nvar img=document.getElementById(\"img24a\");\r\n      \tdocument.getElementById(\"play24a\").onclick=\"\";\r\n      \t       \t interval25= setInterval(function(){\r\n      \t   if(counter25<10)\r\n      \t \timg.src=imgPath1_24a+counter25+\".png\";\r\n      \t   else\r\n      \t   \timg.src=imgPath2_24a+counter25+\".png\";\r\n      \t \tcounter25++;\r\n      \t \tif(counter25>endImage24a) {\r\n      \t \t\tclearInterval(interval25);\r\n      \t \t\tcounter25=0;\r\n      \t \t\tdocument.getElementById(\"play24a\").onclick=play24a;\r\n      \t \t\t//img.src=imgPath1_24a+startImage24a+\".png\";\r\n      \t \t}\r\n\r\n      \t },100);\r\n      }\r\nfunction stop24a(){\r\n      \t \t\tclearInterval(interval25);\r\n      \t \t\tcounter25=0;\r\n      \t \t\tdocument.getElementById(\"play24a\").onclick=play24a;\r\n      \t \t\timg.src=imgPath2_24a+endImage24a+\".png\";\r\n}\r\nvar startImage24b=0;\r\n\t  var endImage24b=56;\r\n      var counter26=0;\r\n      var imgPath1_24b=\"/assets/images/fig24B/ch1_fig24B000\";\r\n      var imgPath2_24b=\"/assets/images/fig24B/ch1_fig24B00\";\r\n\t  var interval26;\r\n      function play24b() {\r\nvar img=document.getElementById(\"img24b\");\r\n    \tdocument.getElementById(\"play24b\").onclick=\"\";\r\n      \t       \t interval26= setInterval(function(){\r\n      \t   if(counter26<10)\r\n      \t \timg.src=imgPath1_24b+counter26+\".png\";\r\n      \t   else\r\n      \t   \timg.src=imgPath2_24b+counter26+\".png\";\r\n      \t \tcounter26++;\r\n      \t \tif(counter26>endImage24b) {\r\n      \t \t\tclearInterval(interval26);\r\n      \t \t\tcounter26=0;\r\n      \t \t\tdocument.getElementById(\"play24b\").onclick=play24b;\r\n      \t \t\t//img.src=imgPath1_24b+startImage24b+\".png\";\r\n      \t \t}\r\n\r\n      \t },100);\r\n      }\r\nfunction stop24b(){\r\n      \t \t\tclearInterval(interval26);\r\n      \t \t\tcounter26=0;\r\n      \t \t\tdocument.getElementById(\"play24b\").onclick=play24b;\r\n      \t \t\timg.src=imgPath2_24b+endImage24b+\".png\";\r\n}\r\nvar startImage29=0;\r\n\t  var endImage29=31;\r\n      var counter27=0;\r\n      var imgPath1_29=\"/assets/images/fig29/ch1_fig29000\";\r\n      var imgPath2_29=\"/assets/images/fig29/ch1_fig2900\";\r\n\t  var interval27;\r\n      function play29() {\r\nvar img=document.getElementById(\"img29\");\r\n      \tdocument.getElementById(\"play29\").onclick=\"\";\r\n      \t       \t interval27= setInterval(function(){\r\n      \t   if(counter27<10)\r\n      \t \timg.src=imgPath1_29+counter27+\".png\";\r\n      \t   else\r\n      \t   \timg.src=imgPath2_29+counter27+\".png\";\r\n      \t \tcounter27++;\r\n      \t \tif(counter27>endImage29) {\r\n      \t \t\tclearInterval(interval27);\r\n      \t \t\tcounter27=0;\r\n      \t \t\tdocument.getElementById(\"play29\").onclick=play29;\r\n      \t \t\t//img.src=imgPath1_29+startImage29+\".png\";\r\n      \t \t}\r\n\r\n      \t },100);\r\n      }\r\nfunction stop29(){\r\n      \t \t\tclearInterval(interval27);\r\n      \t \t\tcounter27=0;\r\n      \t \t\tdocument.getElementById(\"play29\").onclick=play29;\r\n      \t \t\timg.src=imgPath2_29+endImage29+\".png\";\r\n}\r\n</script>\r\n</script>\r\n<script type=\"text/ng-template\" id=\"restricted-objects.html\">\r\n    <h2 class=\"level2\"><a id=\"6\"></a><span>What Happens if We Restrict the Movement of a Free Object?</span></h2>\r\n<p class=\"indent\"><span>When you restrict an object so that it cannot move freely anymore, its center of gravity will move toward the restriction side. This will affect the object’s response to the applied forces. Assume we place one side of a pencil in a wall made of a soft material, such as wax, so that it cannot fall down but can move in response to the applied forces. If the pencil was a free object, the application of force on the center of gravity would push the pencil straight up without tilting (<a href=\"#fig1.21\">Figure 1.21</a>). However, this pencil is not a free object anymore; therefore, the application of a force on the same point will now push the pencil up and tilt it at the same time (<a href=\"#fig1.22\">Figure 1.22</a>). </span></p>\r\n<p class=\"img\"><a id=\"play22\" onclick=\"play22()\"><img id=\"img22\" style=\"max-width:100%;\" src=\"/assets/images/fig22/ch1_fig22_0000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig1.22\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.22. Application of force on the point considered the center of gravity of the pencil when it was free object will now move the pencil up and tilt it at the same time.</i></span></p>\r\n<p class=\"indent\"><span>If we apply a force at a point slightly closer to the wax, the pencil will move in a straight line. It is as though a new center of gravity has been formed. This new point of balance is called the center of resistance (<a href=\"#fig1.23\">Figure 1.23</a>).</span></p>\r\n<p class=\"img\"><a id=\"play23\" onclick=\"play23()\"><img id=\"img23\" style=\"max-width:100%;\" src=\"/assets/images/fig23/ch1_fig230000.png\"/></a></p>\r\n<p class=\"caption\" id=\"fig1.23\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.23. Restricted objects develop new centers of gravity that are called centers of resistance. Application of a force on the center of resistance will move the object in a straight line.</i></span></p>\r\n</script>\r\n<script type=\"text/ng-template\" id=\"tooth.html\">\r\n  <h2 class=\"level2\"><a id=\"7\"></a><span>Tooth as a Restricted Object</span></h2>\r\n<p class=\"indent\"><span>Similar to any free object, a tooth that is out of the bony socket has a center of gravity, but the same tooth when surrounded by alveolar bone has a new balance point that is called the center of resistance. If you can apply a force that passes through this point (with the assumption that the bone around the tooth allows the tooth to move), the tooth will move in a straight line without tilting (<a href=\"#fig1.24\">Figure 1.24</a>). </span></p>\r\n<table style=\"width:100%; margin-bottom:1em\">\r\n<tr>\r\n<td>\r\n<p style=\"float:left;\"><a id=\"play24a\" onclick=\"play24a()\"><img id=\"img24a\" style=\"max-width:100%;\" src=\"/assets/images/fig24A/ch1_fig24A0000.png\"/></a></p>\r\n</td>\r\n<td>\r\n<p style=\"float:right;\"><a id=\"play24b\" onclick=\"play24b()\"><img id=\"img24b\" style=\"max-width:100%;\" src=\"/assets/images/fig24B/ch1_fig24B0000.png\"/></a></p>\r\n</td>\r\n</tr></table>\r\n<p class=\"caption\" id=\"fig1.24\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.24. The center of gravity of a tooth shown as a blue circle, when the tooth is out of the socket (A) will move apically once the tooth is in the bony socket (B) . This new center is called center of resistance (shown as red circle).</i></span></p>\r\n<p class=\"normal\"><span>The center of resistance of a tooth depends on:</span></p>\r\n<p class=\"indent\"><span>1. Number of roots</span></p>\r\n<p class=\"indent\"><span>2. Root length and morphology</span></p>\r\n<p class=\"indent\"><span>3. Level of alveolar bone support</span></p>\r\n<h3 class=\"level3\"><span>Effect of Number of Roots on Center of Resistance</span></h3>\r\n<p class=\"indent\"><span>The center of resistance in a single rooted tooth is estimated to be located at one third of the distance from the alveolar crest toward the apex. Notice that the center of resistance is a three-dimensional point or area. In other words, although it is not a physical point, we can look at it in three dimensions. Therefore, we do not have different centers of resistance in each dimension, but only one point or small area that can be analyzed in all dimensions (<a href=\"#fig1.25\">Figure 1.25</a>).</span></p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/fig25.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig1.25\"><span><i>Figure 1.25. Location of the center of resistance of a single rooted tooth in the three planes of space.</i></span></p>\r\n<p class=\"indent\"><span>Increasing the number of roots will move the center of resistance away from the alveolar crest. For example, the center of resistance of an upper molar appears to be around the root furcation or 2 mm apical to the furcation. From an occlusal view, the center of resistance is located more toward the palatal cusps, where the bulk of the tooth mass is located (<a href=\"#fig1.26\">Figure 1.26</a>).</span></p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/fig26.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig1.26\"><span><i>Figure 1.26. Location of the center of resistance of the upper molar in the three planes of space.</i></span></p>\r\n<p class=\"indent\"><span>The center of resistance of the lower molar is also located closer to the furcation area of the roots whereas, from the occlusal view, it appears closer to the buccal side (<a href=\"#fig1.27\">Figure 1.27</a>).</span></p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/fig27.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig1.27\"><span><i>Figure 1.27. Location of the center of resistance of the lower molar in the three planes of space</i></span></p>\r\n<h3 class=\"level3\"><span>Effect of Root Length and Morphology on the Center of Resistance</span></h3>\r\n<p class=\"indent\"><span>The center of resistance varies with changes in root length and width. In a tooth with a longer root (if periodontium support is intact), the center of resistance is positioned farther apical from the alveolar crest. A similar change is observed when the width of the root increases (<a href=\"#fig1.28\">Figure 1.28</a>).</span></p>\r\n<p class=\"img\"><img style=\"max-width:100%;\" src=\"/assets/images/fig28.png\" alt=\"images\" /></p>\r\n<p class=\"caption\" id=\"fig1.28\"><span><i>Figure 1.28. Estimated position of center of resistance in different teeth. When periodontal support is the same, the center of resistance (red circle) is displaced more apically from the alveolar crest with increased root length or width.</i></span></p>\r\n</script>\r\n<script type=\"text/ng-template\" id=\"moment-force.html\">\r\n  <h2 class=\"level2\"><a id=\"8\"></a><span>Moment of a Force</span></h2>\r\n  <p class=\"indent\"><span>The magnitude of a moment depends on the magnitude of the force and the distance from the center of resistance (<a href=\"#fig1.36\">Figure 1.36</a>)</span></p>\r\n  <p class=\"img\"><a id=\"play36\" onclick=\"play36()\"><img id=\"img36\" style=\"max-width:100%;\" src=\"/assets/images/fig36/ch1_fig360000.png\"/></a></p>\r\n  <p class=\"caption\" id=\"fig1.36\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<span><i>Figure 1.36. Magnitude of a moment.</i></span></p>\r\n  <p class=\"indent\"><span>The magnitude of the moment is equal to the magnitude of force multiplied by the perpendicular distance (shortest distance) of the line of action of the force to the center of resistance.</span></p>\r\n  <p class=\"center0\">M<sub>F</sub> = F &#215; d</p>\r\n  <p class=\"indent\"><span>The units of a moment are a combination of the unit of the force and the unit of the distance, Newton-millimeter or gram force-millimeter.</span></p>\r\n</script>\r\n<script type=\"text/ng-template\" id=\"recommended-reading.html\">\r\n  <h1 class=\"tit_3\"><a id=\"12\"></a><span>Recommend Reading</span></h1>\r\n<p class=\"normal\"><span>1. Isaacson RJ, Lindauer SJ, Davidovitch M. On tooth movement.</span> <i>Angle Orthod</i><span> 1993 Winter;63(4):305&#8211;309.</span></p>\r\n<p class=\"normal\"><span>2. Mulligan TF. Common sense mechanics 2. Forces and moments.</span> <i>J Clin Orthod</i><span> 1979 Oct;13(10):676&#8211;683.</span></p>\r\n<p class=\"normal\"><span>3. Smith RJ, Burstone CJ. Mechanics of tooth movement.</span> <i>Am J Orthod</i><span> 1984 Apr;85(4):294&#8211;307.</span></p>\r\n<p class=\"normal\"><span>4. Matsui S, Caputo AA, Chaconas SJ, Kiyomura H. Center of resistance of anterior arch segment.</span> <i>Am J Orthod Dentofacial Orthop</i><span> 2000 Aug;118(2):171-8.</span></p>\r\n</script>\r\n"

