	module.exports = "<div>\n  <h1 class=\"tit_1\"><a id=\"15\"></a><a id=\"10\"></a>Equivalent System</h1>\n<p class=\"indent\">As described, the effect of a force that does not pass through the center of resistance is a combination of displacement and rotation. But, how can we draw these effects? One way is to just draw the force at the bracket and expect that reader to know that because that force was not applied through the center of resistance it has a hidden tendency to rotate the tooth. The other way is to isolate both the displacement and rotation effects of the force and draw them at the center of resistance.</p>\n<p class=\"indent\">For example, a 100 cN force is applied 5 mm away from the center of resistance. At the center of resistance, the tooth will receive the 100 cN force and also a 500 cN-mm tendency of rotation. To illustrate what the tooth receives at the center of resistance, we can draw the 100 cN force and 500 cN-mm moment at center of resistance (<a href=\"#fig-ch2-5\">Figure 2.5</a>). Because these two diagrams are describing the same phenomenon, these two diagrams are called &#8220;equivalents.&#8221;</p>\n<table style=\"width:100%; margin-bottom:0\">\n<tr>\n<td>\n<p style=\"float:left;\"><a id=\"play8\" onclick=\"play8()\"><img id=\"animation-8\" style=\"max-width:100%;\" src=\"/assets/images/ch2_fig5A/ch2_fig5A0000.png\"/></a></p>\n</td>\n<td>\n<p style=\"float:center;\"><a id=\"play9\" onclick=\"play9()\"><img id=\"animation-9\" style=\"max-width:100%;\" src=\"/assets/images/ch2_fig5B/ch2_fig5B0000.png\"/></a></p>\n</td>\n</tr>\n<tr>\n<td>\n<p class=\"center-laaa\">A</p>\n</td>\n<td>\n<p class=\"center-laaa\">B</p>\n</td>\n</tr>\n</table>\n<p class=\"caption\" id=\"fig-ch2-5\"><span class=\"btn_play\"></span>&#160;&#160;&#160;&#160;&#160;<i>Figure 2.5. Different illustrations of the effect of force and moment at the center of resistance, when the force is applied to the bracket, 5 mm below the center of resistance. Illustration (A) is equivalent and interchangeable with (B) and helps visualize the effect at the center of resistance.</i></p>\n\n</div>\n"

